import React, { useEffect, useCallback, useState, useRef } from 'react';
import * as PIXI from 'pixi.js';
import { Stage } from '@inlet/react-pixi';
import SpineComponent from '../Spine';
import { Spine } from 'pixi-spine';

import { BODYPART_COLOR } from 'constants/HeroAssets/bodyPartColor';

const SpineHero = (props) => {
  const { hero, animation } = props;
  const [spineData, setSpineData] = useState();
  const [animationState, setAnimationState] = useState();
  const slotBase = 'nft_part';

  const container = useRef();

  // console.log('animation:  ', animation);

  useEffect(() => {
    const initSpine = async () => {
      await import('pixi-spine');
      const loader = new PIXI.Loader();

      loader.add('data', hero.animPath, { crossOrigin: true }).load((loader, res) => {
        const data = res?.data?.spineData;
        console.log(hero);
        const _spineData = new Spine(data);

        const skeleton = _spineData.skeleton;

        skeleton.setSkin(null);
        skeleton.setSkinByName('skin_' + hero.form);
        setSpineData(_spineData);

        skeleton.slots.forEach((slot) => {
          if (slot.data.name.includes(slotBase)) {
            const partId = parseInt(slot.data.name.substr(8, 1));

            if (!isNaN(partId) && partId > 0 && partId < 5) {
              slot.color = BODYPART_COLOR[partId];
            }
          }
        });
      });
    };
    initSpine();
  }, []);

  const stateRef = useCallback(
    (state) => {
      if (state && animation) {
        state.setAnimation(0, 'idle', true);
      }
      setAnimationState(state);
    },
    [animation],
  );

  return (
    <div ref={container} {...props}>
      <Stage
        width={container.current?.clientWidth}
        height={container.current?.clientHeight}
        options={{ transparent: true, autoDensity: true }}
      >
        {spineData && (
          <SpineComponent
            x={container.current?.clientWidth / 2}
            y={container.current?.clientHeight / 1.15}
            spineData={spineData}
            scale={0.3}
            animationStateCallback={stateRef}
          />
        )}
      </Stage>
    </div>
  );
};

export default SpineHero;
