import { DataView } from 'primereact/dataview';
import React, { useCallback } from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { get, post } from 'services/httpServices';
import iconNature from '../../assets/images/icon-nature.png';
import iconCart from '../../assets/images/icon-cart.png';
import iconMage from '../../assets/images/icon-mage.png';
import iconBinance from '../../assets/images/icon-binance64x64.png';
import frameBuy from '../../assets/images/frame-buy.png';
import frameItem from '../../assets/images/frame-item.png';
import backgroundItem from '../../assets/images/background-item.png';
import footerItem from '../../assets/images/footer-item.png';
import crossBar from '../../assets/images/cross-bar.png';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import { RouteBase } from 'constants/routeUrl';
import SpineHero from 'components/SpineHero';
import { Checkbox } from 'primereact/checkbox';
import { Slider } from 'primereact/slider';
import images from 'assets/images';
import { InputSwitch } from 'primereact/inputswitch';
import { Button } from 'primereact/button';
import { API } from 'constants/api';
import { HERO_TYPE } from 'constants/heroType';
import { HERO_NAME } from 'constants/HeroAssets/heroName';
import { HERO_FACTION, HERO_CLASS, HERO_SPINE } from 'constants/HeroAssets/heroMapping';
import { useHistory } from 'react-router';
import { useSelector } from 'react-redux';
import { random } from 'lodash';
import TimerDebounce from 'helpers/timer';
import heroesMarketService from 'services/heroesMarketService';
import { ethers } from 'ethers';

const timerDebounce = new TimerDebounce();

const Marketplace = (props) => {
  const history = useHistory();
  const accountAddress = useSelector((state) => state.authReducer.account);
  const [data, setData] = useState([]);
  const [selectedCity1, setSelectedCity1] = useState(null);
  const [heroTypeId, setHeroTypeId] = useState('');
  const [value2, setValue2] = useState('');
  const [valueOwned, setValueOwned] = useState(false);
  const [valueOffer, setValueOffer] = useState(false);
  const [valueStar, setValueStar] = useState([1, 12]);
  const [summonPoint, setSummonPoint] = useState(10);
  const [attackPart, setAttackPart] = useState(10);
  const [critPart, setCritPart] = useState(10);
  const [hpPart, setHpPart] = useState(10);
  const [defensePart, setDefensePart] = useState(10);
  const [dataHero, setDataHero] = useState([]);

  const [formStart, setformStart] = useState(1);
  const [formEnd, setformEnd] = useState(1);
  const [isLoading, setLoading] = useState(false);
  const [checked1, setChecked1] = useState(false);
  const [checked2, setChecked2] = useState(false);

  const [pagingParam, setPagingParam] = useState({
    // first: 0,
    rows: 9,
    page: 0,
  });
  const [totalRecords, setTotalRecords] = useState(0);

  const [heroCount, SetHeroCount] = useState(0);

  const cities = [
    { name: 'Highest ID', code: 'HI' },
    { name: 'Lowest ID', code: 'LI' },
    { name: 'Highest Price', code: 'HP' },
    { name: 'Lowest Price', code: 'LP' },
    { name: 'Highest Star', code: 'HS' },
    { name: 'Lowest Star', code: 'LS' },
    { name: 'Recently Listed', code: 'RL' },
    { name: 'Recently Sold', code: 'RS' },
    { name: 'Recently Offered', code: 'RO' },
  ];

  useEffect(() => {
    timerDebounce.debounce(() => {
      const getListHero = async () => {
        setLoading(true);
        const params = {
          nftOwnerId: valueOwned ? accountAddress : '',
          heroTypeId: Number(heroTypeId),
          // criticalRatePartStart: 1,
          // criticalRatePartEnd: 10,
          formStart: formStart,
          starStart: valueStar[0],
          summonPointStart: 0,
          attackPartStart: 1,
          hpPartStart: 1,
          defensePartStart: 1,
          formEnd: formEnd,
          attackPartEnd: attackPart,
          critPartEnd: critPart,
          hpPartEnd: hpPart,
          defensePartEnd: defensePart,
          summonPointEnd: summonPoint,
          starEnd: valueStar[1],
        };
        try {
          const listHero = await post(
            `${API.BASE_URL + API.GET_HERO_LIST}?page=${pagingParam.page}&size=${pagingParam.rows}`,
            params,
          );
          setTotalRecords(listHero.data.data.totalItems);
          const dataHero = listHero.data.data.heroes;

          for (let i = 0; i < dataHero.length; i++) {
            const price = await heroesMarketService.getPriceHero(dataHero[i].id);
            const convertPrice = ethers.utils.formatEther(price);
            dataHero[i].price = convertPrice;
          }
          setDataHero(dataHero);
        } catch (error) {
          console.log('error', error);
        } finally {
          setLoading(false);
        }
      };
      getListHero();
    }, 700);
  }, [
    summonPoint,
    heroTypeId,
    valueStar,
    attackPart,
    critPart,
    hpPart,
    defensePart,
    formStart,
    formEnd,
    valueOwned,
    accountAddress,
    pagingParam,
  ]);

  const onCityChange = (e) => {
    setSelectedCity1(e.value);
  };

  const goDetailHero = (convertData, data) => {
    history.push({
      pathname: RouteBase.DetailHero,

      state: {
        dataDetail: { ...data, ...convertData },
      },
    });
  };

  const onPage = (event) => {
    const _pagingParam = { ...pagingParam, ...event };
    setPagingParam(_pagingParam);
  };

  const componentCheckBox = (id, img, title) => {
    return (
      <div className="p-field-checkbox">
        <Checkbox inputId={id} onChange={() => {}} />
        <img src={img} alt="" />
        <label className="p-ml-0" htmlFor={id}>
          {title}
        </label>
      </div>
    );
  };

  const compBodyPartSwitch = (title, value, setValue) => {
    return (
      <div className="body-part-container p-d-flex">
        <div className="label-item-body-part">{title}</div>
        <div className="body-part-switch">
          <Slider value={value} min={0} max={10} onSlideEnd={(e) => setValue(e.value)} />
          <div className="p-d-flex p-jc-between">
            <div className="value-switch">0</div>
            <div className="value-switch">{value}</div>
          </div>
        </div>
      </div>
    );
  };

  const clearFilter = () => {
    setValueStar([1, 12]);
    setSummonPoint(10);
    setAttackPart(10);
    setCritPart(10);
    setHpPart(10);
    setDefensePart(10);
    setChecked1(false);
    setChecked2(false);
  };

  const checkCommon = (e) => {
    setformStart(1);
    setformEnd(2);
    setChecked1(e.checked);
    setChecked2(false);
  };
  const checkMystic = (e) => {
    setformStart(3);
    setformEnd(3);
    setChecked2(e.checked);
    setChecked1(false);
  };
  const renderFilter = () => {
    return (
      <div className="filter-item">
        <div className="filter-content">
          <div className="filter-header p-d-flex p-jc-between p-ai-center">
            <div>filter</div>
            <Button label="Clear Filter" className="p-button-text" onClick={clearFilter} />
          </div>
          <div className="form">
            <h3>Faction</h3>
            <div className="filter-content-item">
              {componentCheckBox('nature', images.icon_faction_nature, 'Nature')}
              {componentCheckBox('fire', images.icon_faction_fire, 'Fire')}
              {componentCheckBox('light', images.icon_faction_light, 'Light')}
              {componentCheckBox('abyss', images.icon_faction_abyss, 'Abyss')}
              {componentCheckBox('dark', images.icon_faction_dark, 'Dark')}
              {componentCheckBox('water', images.icon_faction_water, 'Water')}
            </div>
          </div>
          <img className="cross-bar" alt="" src={crossBar} />
          <div className="form">
            <h3>Class</h3>
            <div className="filter-content-item">
              {componentCheckBox('mage', images.icon_class_mage, 'Mage')}
              {componentCheckBox('ranger', images.icon_class_ranger, 'Ranger')}
              {componentCheckBox('warrior', images.icon_class_warrior, 'Warrior')}
              {componentCheckBox('assassin', images.icon_class_assassin, 'Assassin')}
              {componentCheckBox('priest', images.icon_class_priest, 'Priest')}
            </div>
          </div>
          <img className="cross-bar" alt="" src={crossBar} />
          <div className="form">
            <h3>Rarity</h3>
            <div className="filter-content-item">
              <div className="p-field-checkbox">
                <Checkbox inputId="binary" checked={checked1} onChange={checkCommon} />
                <label htmlFor="binary">Common</label>
              </div>
              <div className="p-field-checkbox">
                <Checkbox inputId="binary" checked={checked2} onChange={checkMystic} />
                <label htmlFor="binary">Mystic</label>
              </div>
            </div>
          </div>
          <img className="cross-bar" alt="" src={crossBar} />
          <div className="form">
            <h3>Star</h3>
            <div className="p-d-flex p-jc-between">
              <div className="value-switch">{valueStar[0]}</div>
              <div className="value-switch">{valueStar[1]}</div>
            </div>
            <Slider value={valueStar} min={1} max={12} onChange={(e) => setValueStar(e.value)} range />
          </div>
          <div className="form">
            <h3>Summon Point</h3>
            <div className="p-d-flex p-jc-between">
              <div className="value-switch">0</div>
              <div className="value-switch">{summonPoint}</div>
            </div>
            <Slider value={summonPoint} min={0} max={10} onChange={(e) => setSummonPoint(e.value)} />
          </div>
          {/* <img className="cross-bar" alt="" src={crossBar} /> */}
          <div className="filter-slider"></div>
          <div className="form">
            <h3>Body Part</h3>
            {compBodyPartSwitch('Attack Part', attackPart, setAttackPart)}
            {compBodyPartSwitch('Crit. Rate Part', critPart, setCritPart)}
            {compBodyPartSwitch('Defend Part', defensePart, setDefensePart)}
            {compBodyPartSwitch('HP Part', hpPart, setHpPart)}
          </div>
        </div>
      </div>
    );
  };

  const arrStar = [];
  arrStar.push({});

  const renderHeroStar = (star) => {
    return [...Array(star)].map((e, i) => (
      <span key={i}>
        <img className="image-star" src={i < 6 ? images.icon_star_yellow : images.icon_star_violet} alt="" />
      </span>
    ));
  };

  const renderGridItem = (data) => {
    // const convertData = HERO_TYPE.filter((elm) => elm.id === data.heroTypeId)[0] ?? HERO_TYPE[0];

    const heroName = HERO_NAME[data.heroTypeId];
    const heroFaction = HERO_FACTION[heroName.factionID];
    const heroClass = HERO_CLASS[heroName.classID];

    // const animKey = '20026_1';
    const animKey = data.heroTypeId + '_' + data.form;

    const getFirstAnimSameFaction = (factionID) => {
      const a = ['10013_1', '10013_1', '20019_1', '30015_1', '60017_1', '50016_1', '60017_1'];
      return a[factionID];
    };

    const animData = {
      animPath: HERO_SPINE.hasOwnProperty(animKey)
        ? HERO_SPINE[animKey]
        : HERO_SPINE[getFirstAnimSameFaction(heroName.factionID)],
      bodyPartLevels: [data.attackPart, data.criticalRatePart, data.defensePart, data.hpPart],
      bg: heroFaction.bg,
      form: data.form,
    };

    const convertData = {
      heroName: heroName,
      heroFaction: heroFaction,
      heroClass: heroClass,
      animData: animData,
      // form: data.form,
      // summonPoint: data.summonPoint,
    };

    // sample prices
    const rand = (Math.round(random(0.05, 1) * 100) / 100).toFixed(2);
    const priceStr = rand > 0.5 ? '--' : rand.toString() + ' BNB';
    const priceUSDStr = rand > 0.5 ? '' : (rand * 500).toString() + '$';

    return (
      <div onClick={() => goDetailHero(convertData, data)} className="p-col-12 p-md-4">
        <div className="container-item">
          <div className="content-up">
            <img className="background-item" src={heroFaction.bg} alt="" />
            {/* <img className="footer-item" src={footerItem} alt="" /> */}
            <div className="container-hero-star">{renderHeroStar(data.star)}</div>
            <SpineHero hero={animData} className="element-canvas" />
            <div className="item-data">
              <img className="item-element" src={heroFaction.icon} alt="" />
              <div className="item-gender">{heroFaction.name}</div>
              <img className="item-element" src={heroClass.icon} alt="" />
              <div className="item-gender">{heroClass.name}</div>
            </div>
          </div>
          <div className="content-down">
            <div className="content-down-left">
              <div className="id">#{data.id}</div>
              <div className="name">{heroName.name}</div>
              <div className="point">Summon Point: {data.summonPoint}</div>
            </div>
            <div className="content-down-right">
              <div className="price">
                <img src={iconBinance} alt="" />
                <div className="hero-token">{data.price === '0.0' ? '--' : data.price}</div>
              </div>
              <div className="hero-money">{priceUSDStr}</div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  const renderContent = () => {
    return (
      <div>
        <div className="market-search p-d-flex p-jc-between">
          <div className="p-search">
            {/* <div className="p-input-icon-left">
              <i className="pi pi-search" />
              <InputText value={value2} onChange={(e) => setValue2(e.target.value)} placeholder="Search" />
            </div> */}

            <div className="content-down-info">
              <h3 className="amount-total"> {totalRecords} HEROES</h3>
            </div>
          </div>
          <div className="filter-header p-d-flex p-jc-between">
            <Dropdown
              value={selectedCity1}
              options={cities}
              onChange={onCityChange}
              optionLabel="name"
              placeholder="Sort By"
            />
            <InputText
              className="input-number-id custom-number-heroid"
              type="number"
              value={heroTypeId}
              onChange={(e) => setHeroTypeId(e.target.value)}
              placeholder="Hero ID"
            />
          </div>
        </div>
        <div className="data-view">
          <React.Suspense fallback={<h1>Loading posts...</h1>}>
            <React.Fragment>
              {isLoading ? (
                <div className="testspinner">
                  <i className="pi pi-spin pi-spinner"></i>
                </div>
              ) : (
                <DataView
                  value={dataHero}
                  layout="grid"
                  itemTemplate={(hero) => renderGridItem(hero)}
                  paginator
                  rows={pagingParam.rows}
                  totalRecords={totalRecords}
                  // first={pagingParam.first}
                  onPage={onPage}
                />
              )}
            </React.Fragment>
          </React.Suspense>
        </div>
      </div>
    );
  };

  return (
    <div className="market p-grid">
      <div className="market__group-switch p-grid p-col-12 p-justify-end p-mb-3">
        <div className="p-mr-6">
          <div>Owned</div>
          <InputSwitch checked={valueOwned} onChange={(e) => setValueOwned(e.value)} />
        </div>
        <div>
          <div>Your Offer</div>
          <InputSwitch checked={valueOffer} onChange={(e) => setValueOffer(e.value)} />
        </div>
      </div>
      {/* <div className="p-grid"> */}
      <div className="market__filter-container">{renderFilter()}</div>
      <div className="market__content">{renderContent()}</div>
      {/* </div> */}
    </div>
  );
};
export default Marketplace;
